import React from 'react'
import {Link} from 'react-router-dom'
import {Button,Input
} 
from 'antd';

import './css/about.css'
class AboutPanel extends React.Component{
    render(){
        return(
            <div className='about-panel'>
                <h3 className='about-title'>
                    {this.props.title}
                </h3>
                {this.props.subTitle}
                <p>
                    {this.props.desc}
                </p>
                {this.props.children}
            </div>
        )
    }
}
class About extends React.Component{
    constructor(props){
        super(props);
        this.state = {input:''}
    }
    handleInput = (e)=>{
        console.log(e.target.value);
        this.setState({
            input:e.target.value
        })
    }
    render(){
        return(
            <div className='about'>
                <Link to="/">Home</Link>
                <h2>关于我</h2>
                <AboutPanel title = "自我介绍" desc = "111"/>
                <AboutPanel title = "兴趣爱好" desc = "222">
                <Input type ="text" value = {this.state.input} onChange={this.handleInput}></Input>
                
                <Button onClick={()=>{alert(this.state.input)}}>哈哈</Button>
                </AboutPanel>
                <AboutPanel title = "工作履历" desc = "333" subTitle = {<h4>经历丰富行业跨度大</h4>}/>
            </div>
        )
    } 
}

export default About;