import React from 'react'
import ReactDom from 'react-dom'
import './css/counter.css'
import AddItem from './additem'
import TodoItem from './todoItem'
import './css/index.css'
import About from './about'
import { BrowserRouter as Router,Route,Link } from 'react-router-dom'
const TODO_KEY = "todos"
class TodoComponent extends React.Component{
  constructor(props){
    super(props);
    const data = localStorage.getItem(TODO_KEY);
    this.state = {
      todos:JSON.parse(data) || []
  }
  }
  handleAdd = (val)=>{
    console.log(val);
    let todos = this.state.todos;
    todos.push(val);
    this.setState({
      todos
    })
    localStorage.setItem(TODO_KEY,JSON.stringify(todos));
  }
  handleDelete = (index) =>{
    let todos = this.state.todos;
    todos=todos.filter((val,i)=>{
      return index !==i
    })
    this.setState({
      todos
    })
    localStorage.setItem(TODO_KEY,JSON.stringify(todos));
  }
  
  render(){
    let subTitle = null
    let length =this.state.todos.length;
    if(length%2 ===0){
      subTitle = <p>明日复明日，明日何其多</p>
    }else{
      subTitle = <p>今日事，今日毕</p>
    }
    console.log("begin");
    const list = this.state.todos.map((val,index)=>{
      return <TodoItem key = {index} index ={index} val = {val} onDelete = {this.handleDelete}/>
    })
    return(
      <div className='todo-list'>
        <Link to = "/about">关于About</Link>
        {this.props.showTitle &&<h2>GTD记事本</h2>}
        {subTitle}
        {length ===0? <p>恭喜你 任务完成</p>:
        <ul>
            {list}
        </ul>}
        <AddItem onInputChange = {this.handleAdd}/>
      </div>
    )
  }
}
class App extends React.Component{
  render(){
    return(
      <Router>
        <div>
          <Route path = "/" exact ={true} render={()=><TodoComponent showTitle={true}/>}></Route>
          <Route path = "/about" component = {About}></Route>
        </div>
      </Router>
    )
  }
}

ReactDom.render(
  <App showTitle ={false}/>,
  document.getElementById('root')
)